* {
	box-sizing: border-box;
}

html, body {
	margin: 0;
	padding: 0;
	font-family: 'Inter', sans-serif;
}

.nav-bar {
	background: #F55A5A;
	color: #fff;
	padding: 15px;
	display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    gap: 7px;
}

.nav-bar--text {
	font-weight: 500;
	font-size: 14.4608px;
	line-height: 18px;
	letter-spacing: -0.075em;
}

main {
	padding: 45px 40px;
}
.travel-node {
	display: flex;
	width: 100%;
}

.travel-node--details {
	padding: 20px;
}
.travel-node--image-contianer {
	width: 125px;
}

.travel-node--image-contianer > img {
	width: 125px;
	height: 100%;
	object-fit: cover;
	border-radius: 10px;
	padding: 5px 0;
}
.travel-node--location {
	display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 3px;
}

.travel-node--l-country {
	font-weight: 400;
	font-size: 10.24px;
	line-height: 12px;
	letter-spacing: 0.17em;
	color: #2B283A;
}

.travel-node--l-google {
	font-weight: 400;
	font-size: 10.24px;
	line-height: 12px;
	text-decoration-line: underline;
	color: #918E9B;
	margin-left: 3px;
}

.travel-node--title-container {
	margin-top: 7px;
}

.travel-node--title-container > h2 {
	margin: 0;
	font-weight: 700;
	font-size: 25px;
	line-height: 30px;
	color: #2B283A;
}

.travel-node--date-container {
	margin-top: 21px;
}

.travel-node--date-container > span {
	font-weight: 700;
	font-size: 10.24px;
	line-height: 12px;
	color: #2B283A;
}
.travel-node--desp-container {
	margin-top:7px;
}

.travel-node--desp-container > p {
	margin: 0;
	font-weight: 400;
	font-size: 10.24px;
	line-height:  15px;
	color: #2B283A;
}
